body {
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background-image: url(../../static/media/background.da386a91.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
#root{
  width: 100%;
}
/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */

.Player{
    padding: 0 10px;
    background: rgba(0, 0, 0, 0.3);
    color: white;
    box-shadow: 0 0 1px 0px white;
    font-size: 20px;
    line-height: 20px;
}

.active-player{
    background: rgba(0, 0, 0, 0.8);
    box-shadow: 0 0 20px 1px white;
}

.Player-1{
    order: 0;
}

.Player-2{
    order: 2;
}
.Dice {
    margin: auto;
    width: 160px;
    height: 160px;
    -webkit-perspective: 600px;
            perspective: 600px;
}
.cube {
    width: 100%;
    height: 100%;
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translateZ(-100px);
            transform: translateZ(-100px);
    -webkit-transition: 1s;
    transition: 1s;
  }
  
.cube__face {
    position: absolute;
    width: 160px;
    height: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
}


.cube__face--front  { 
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    -webkit-transform: rotateY(  0deg) translateZ(80px);
            transform: rotateY(  0deg) translateZ(80px);  
}
.cube__face--right  { 
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    -webkit-transform: rotateY( 90deg) translateZ(80px);
            transform: rotateY( 90deg) translateZ(80px);
 }
.cube__face--back   { 
    background-color: rgba(0, 0, 0, 0.9);;
    color: white;
    -webkit-transform: rotateY(180deg) translateZ(80px);
            transform: rotateY(180deg) translateZ(80px);
 }
.cube__face--left   { 
    background-color: rgba(0, 0, 0, 0.9);
    color: white;
    -webkit-transform: rotateY(-90deg) translateZ(80px);
            transform: rotateY(-90deg) translateZ(80px);
 }
.cube__face--top    { 
    background-color: rgba(0, 0, 0, 0.9);
    color: white;
    -webkit-transform: rotateX( 90deg) translateZ(80px);
            transform: rotateX( 90deg) translateZ(80px);
 }
.cube__face--bottom { 
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    -webkit-transform: rotateX(-90deg) translateZ(80px);
            transform: rotateX(-90deg) translateZ(80px);
 }


.cube.show-front  { -webkit-transform: translateZ(-100px) rotateY(   0deg); transform: translateZ(-100px) rotateY(   0deg); }
.cube.show-right  { -webkit-transform: translateZ(-100px) rotateY( -90deg); transform: translateZ(-100px) rotateY( -90deg); }
.cube.show-back   { -webkit-transform: translateZ(-100px) rotateY(-180deg); transform: translateZ(-100px) rotateY(-180deg); }
.cube.show-left   { -webkit-transform: translateZ(-100px) rotateY(  90deg); transform: translateZ(-100px) rotateY(  90deg); }
.cube.show-top    { -webkit-transform: translateZ(-100px) rotateX( -90deg); transform: translateZ(-100px) rotateX( -90deg); }
.cube.show-bottom { -webkit-transform: translateZ(-100px) rotateX(  90deg); transform: translateZ(-100px) rotateX(  90deg); }

.bounc{
    -webkit-animation: bounc .6s 1;
            animation: bounc .6s 1;
}
@-webkit-keyframes bounc {
    0% {-webkit-transform: scale(1);transform: scale(1)}
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        box-shadow: 0px 4px 10px 2px grey;
    }
    100% {-webkit-transform: scale(1);transform: scale(1)}
}
@keyframes bounc {
    0% {-webkit-transform: scale(1);transform: scale(1)}
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        box-shadow: 0px 4px 10px 2px grey;
    }
    100% {-webkit-transform: scale(1);transform: scale(1)}
}


.Message {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, 0%) scale(0);
            transform: translate(-50%, 0%) scale(0);

    z-index: 1;
}

.Message__text{
    font-size: 80px;
    background-image: -webkit-linear-gradient(left ,transparent,black, black, transparent);
    background-image: linear-gradient(to right ,transparent,black, black, transparent);
    color: white;
}

.DisplayRoll{
    -webkit-animation: msgDisplay .9s 1;
            animation: msgDisplay .9s 1;
}
.DisplayWinner{
    font-size: 60px;
    color: green;
    -webkit-animation: msgDisplay .9s 1;
            animation: msgDisplay .9s 1;
}

@-webkit-keyframes msgDisplay {
    0% {-webkit-transform: translate(-50%, 0%) scale(0);transform: translate(-50%, 0%) scale(0);}
    50% {-webkit-transform: translate(-50%, 0%) scale(1);transform: translate(-50%, 0%) scale(1);}
    100% {-webkit-transform: translate(-50%, 0%) scale(0);transform: translate(-50%, 0%) scale(0);}
}

@keyframes msgDisplay {
    0% {-webkit-transform: translate(-50%, 0%) scale(0);transform: translate(-50%, 0%) scale(0);}
    50% {-webkit-transform: translate(-50%, 0%) scale(1);transform: translate(-50%, 0%) scale(1);}
    100% {-webkit-transform: translate(-50%, 0%) scale(0);transform: translate(-50%, 0%) scale(0);}
}

.Game {
    margin: auto;
    width: 70%;
    max-width: 800px;
    min-width: 360px;
    position: relative;
    background-image: url(../../static/media/jigsaw.0ec701e0.svg), -webkit-linear-gradient(top, rgba(0, 0, 0, 0.8), rgba(255, 251, 251, 0.1));
    background-image: url(../../static/media/jigsaw.0ec701e0.svg), linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(255, 251, 251, 0.1));
    box-shadow: 0 0 100px 0px black;
    padding: 20px;
    box-sizing: border-box;
    -webkit-transition: -webkit-transform 0.1s;
    transition: -webkit-transform 0.1s;
    transition: transform 0.1s;
    transition: transform 0.1s, -webkit-transform 0.1s;
}
.Players {
    width: 100%;
    margin: 20px 0;
    display: flex;
    justify-content: space-evenly;
}


.winScore-container{
    order: 1;
    color: white;
    background-image: -webkit-linear-gradient(left ,transparent, black, transparent);
    background-image: linear-gradient(to right ,transparent, black, transparent);
    align-self: flex-start;
}

.score-container{
    width: 80%;
    margin: auto;
    padding: 10px 0;
    background-image: -webkit-linear-gradient(left ,transparent, black, transparent);
    background-image: linear-gradient(to right ,transparent, black, transparent);
}

.score{
    color: white;
    font-size: 30px;
    line-height: 0;
}

.btn{
    font-family: inherit;
    font-size: 16px;
    padding: 5px 20px;
    background: transparent;
    color: white;
}

.btn-roll{
    margin-right: 10px;
    color: black;
    background: rgba(255, 255, 255, 0.5);
    border: 1px solid black;
}

.btnPlayAgain{
    display: none;
}
.playAgain{
    margin: auto;
    display: block;
    color: white;
    background: rgba(0, 0, 0, 0.8);
}
.msg-play-again{
    background-image: -webkit-linear-gradient(left ,transparent, black, transparent);
    background-image: linear-gradient(to right ,transparent, black, transparent);
    color: white;
}

.App {
  text-align: center;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
